import { makeStyles } from "@material-ui/core";

// menu classes
const useStyles = makeStyles((theme) => ({
  [theme.breakpoints.up("xs")]: {
    header: {
      backgroundColor: "#f3f7f9",
      padding: "30px",
      width: "100%",

      "& .content": {
        width: "100%",
        margin: "0 auto",
      },

      "& .logo-wrapper": {
        justifyContent: "center",

        "& .logo": {
          width: "165px",
          minWidth: "165px",
        },
      },
    },
  },
  [theme.breakpoints.up("sm")]: {
    header: {
      "& .content": {
        width: "95%",
      },

      "& .logo": {
        marginRight: "34px",
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    header: {
      backgroundColor: "#FFFFFF",
      borderBottom: "1px solid #EFEFEF",
      display: "flex",
      height: "66px",
      padding: 0,
      position: "fixed",
      top: 0,
      left: 0,
      width: "100%",
      zIndex: 1000,

      "& .content": {
        width: "90%",
        maxWidth: "1100px",

        "& .logo-wrapper": {
          justifyContent: "flex-start",

          "& .logo": {
            width: "152px",
            minWidth: "152px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    header: {
      "& .content": {
        maxWidth: "80%",
      },
    },
  },
  [theme.breakpoints.up("xl")]: {
    header: {
      "& .content": {
        width: "70%",
      },
    },
  },
}));

export default useStyles;
