import { makeStyles } from "@material-ui/core";

export default makeStyles((theme) => ({
  "setup-process": {
    backgroundColor: "#f3f7f9",
    display: "flex",
    flexDirection: "column",
  },

  setup: {
    backgroundColor: "#ffffff",
    borderTopLeftRadius: "24px",
    borderTopRightRadius: "24px",
    boxShadow: "0 -4px 5px 0 rgb(0 0 0 / 8%)",
    flexGrow: 1,

    "& .content": {
      height: "100%",

      "& .block-right": {
        display: "flex",
        flexDirection: "column",
        height: "100%",

        "& .form-header": {
          alignItems: "flex-start",
          display: "flex",
          padding: "15px 0 26px",

          "& .MuiIconButton-root": {
            marginLeft: "15px",
            padding: "4px",
          },

          "& .header-text": {
            margin: "auto 0",

            "& h1": {
              color: "#3b414a",
              fontSize: "24px",
              fontWeight: 500,
              lineHeight: 1.17,
            },
          },
        },

        "& form": {
          "& h3": {
            color: "#2E3239",
            fontWeight: 500,
            fontSize: "24px",
            lineHeight: "28px",
            marginBottom: "24px",
          },

          "& p": {
            color: "#2E3239",
            fontWeight: 400,
            fontSize: "16px",
            lineHeight: "19px",
            opacity: 0.7,
            marginBottom: "30px",
          },

          "& .fields": {
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",

            "& .form-label": {
              marginRight: 0,

              "& .MuiFormControlLabel-label": {
                color: "#000000",
                fontSize: "14px",
                fontWeight: 400,
                lineHeight: "16px",
                "-webkit-touch-callout": "none",
                "-webkit-user-select": "none",
                "-khtml-user-select": "none",
                "-moz-user-select": "none",
                "-ms-user-select": "none",
                "user-select": "none",
              },
            },

            "& .file-input": {
              marginTop: "20px",
            },

            "& .form-input": {
              width: "100%",
              marginTop: "20px",

              "& label.Mui-focused": {
                color: "#000000;",
                opacity: 0.5,
              },

              "& .MuiFilledInput-root": {
                borderRadius: "8px",

                "& fieldset": {
                  borderColor: "#000000;",
                  opacity: 0.1,
                  borderRadius: "5px !important",
                },

                "&:hover fieldset": {
                  borderColor: "#000000;",
                  opacity: 0.3,
                },

                "&.Mui-focused fieldset": {
                  borderColor: "#000000;",
                  opacity: 0.3,
                },
              },

              "& .MuiFilledInput-underline:after": {
                border: "none !important",
              },

              "& .MuiFilledInput-underline:before": {
                border: "none !important",
              },

              "& .MuiInputLabel-filled": {
                transform: "translate(16px, 20px) scale(1)",

                "&.MuiInputLabel-shrink": {
                  transform: "translate(16px, 10px) scale(0.75)",
                },
              },

              "& input": {
                backgroundColor: "#F2F3F7!important",
                borderRadius: "8px",

                color: "#7B8AA2",
                paddingLeft: "16px",
                paddingRight: "16px",

                "&:focus": {
                  border: "none",
                  boxShadow: "none",
                },
              },
            },

            "& .select-input": {
              backgroundColor: "#F2F3F7",
              borderRadius: "8px",
              width: "100%",
              marginTop: "20px",
              padding: "0 16px",

              "& .MuiSelect-select": {
                color: "#7B8AA2",
                alignItems: "center",
                display: "flex",
                height: "52px",
                padding: 0,

                "&:focus": {
                  backgroundColor: "#F2F3F7",
                },

                "& em": {
                  fontStyle: "normal",
                },
              },

              "& .MuiOutlinedInput-notchedOutline": {
                border: "none",
              },

              "&:hover ": {
                "& .MuiOutlinedInput-notchedOutline": {
                  border: "none",
                },
              },

              "& .MuiSelect-icon": {
                right: "8px",
              },
            },

            "& .card": {
              background: "#FFFFFF",
              border: "1px solid rgba(0, 0, 0, 0.1)",
              boxSizing: "border-box",
              borderRadius: "20px",
              marginBottom: "30px",

              "&.engage": {
                boxShadow: "0px 0px 20px rgba(18, 176, 238, 0.2)",

                "& .card-body": {
                  "& .MuiButton-root": {
                    background: "#12B0EE",
                    boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
                  },
                },
              },

              "&.curate": {
                boxShadow: "0px 0px 20px rgba(255, 83, 176, 0.2)",

                "& .card-body": {
                  "& .MuiButton-root": {
                    background: "#FF53B0",
                    boxShadow: "0px 8px 8px rgba(247, 93, 52, 0.2)",
                  },
                },
              },

              "& .card-body": {
                alignItems: "center",
                display: "flex",
                flexDirection: "column",
                textAlign: "center",
                padding: "20px",

                "& .icon": {
                  marginBottom: "14px",
                },

                "& .title": {
                  color: "#24272C",
                  fontSize: "16px",
                  fontWeight: 600,
                  lineHeight: "19px",
                  marginBottom: "14px",
                },

                "& .info": {
                  color: "#24272C",
                  fontSize: "14px",
                  fontWeight: 400,
                  lineHeight: "16px",
                  marginBottom: "30px",
                  opacity: 0.7,
                },

                "& .MuiButton-root": {
                  margin: 0,
                },
              },
            },

            "& .mini-text": {
              color: "#7B8AA2",
              fontSize: "10px",
              fontWeight: 400,
              lineHeight: "12px",

              "& strong": {
                fontWeight: 900,
              },
            },

            "& .details": {
              display: "flex",
              flexDirection: "column",

              "& h5": {
                color: "#24272C",
                fontSize: "18px",
                fontWeight: 500,
                lineHeight: "21px",
              },

              "& hr": {
                marginTop: "8px",
                marginBottom: "14px",
              },

              "& .particulars": {
                alignItems: "center",
                display: "flex",
                justifyContent: "space-between",
                marginBottom: "16px",

                "& p": {
                  color: "#566171",
                  fontSize: "15px",
                  fontWeight: 400,
                  lineHeight: "18px",
                  marginBottom: 0,

                  "&.amount": {
                    textAlign: "right",

                    "& strong": {
                      fontWeight: 500,
                    },

                    "& small": {
                      display: "block",
                    },
                  },
                },

                "& .discount-input": {
                  background: "#F2F3F7",
                  borderRadius: "4px",
                  border: "none",
                  boxShadow: "none",
                  color: "#7B8AA2",
                  height: "36px",
                  maxWidth: "99px",
                  outline: "none",
                  padding: "0 12px",
                },

                "& .discount-value": {
                  color: "#47C055",
                  fontSize: "15px",
                  fontWeight: 400,
                  lineHeight: "24px",
                },

                "& .discount-card": {
                  alignItems: "center",
                  background: "#F2F3F7",
                  borderRadius: "4px",
                  display: "flex",
                  height: "26px",
                  justifyContent: "space-between",
                  padding: "0 10px",
                  width: "100px",

                  "& small": {
                    color: "#7B8AA2",
                    fontSize: "13px",
                    fontWeight: 400,
                    lineHeight: "15px",
                  },

                  "& button": {
                    background: "transparent",
                    margin: 0,
                    padding: 0,
                  },
                },
              },
            },

            "& .box-wrapper": {
              backgroundColor: "#F6F9FB",
              borderRadius: "8px",
              display: "flex",
              flexDirection: "column",
              marginTop: "10px",
              marginBottom: "16px",
              padding: "16px",
              rowGap: "10px",

              "& .box-title": {
                color: "#7B8AA2",
                fontSize: "15px",
                fontWeight: 500,
                lineHeight: "18px",
              },

              "& .billing-header": {
                alignItems: "center",
                display: "flex",
                justifyContent: "space-between",

                "& .billing-title": {
                  color: "#7B8AA2",
                  fontSize: "15px",
                  fontWeight: 500,
                  lineHeight: "18px",
                },

                "& .edit-action": {
                  background: "transparent",
                  color: "#7B8AA2",
                  cursor: "pointer",
                  fontSize: "11px",
                  fontWeight: 400,
                  lineHeight: "13px",
                  padding: 0,
                  textDecoration: "underline",
                },
              },

              "& .billing-desc-wrapper": {
                "& .billing-desc": {
                  color: "#7B8AA2",
                  fontSize: "13px",
                  fontWeight: 400,
                  lineHeight: "15px",
                },
              },

              "& .plan-box": {
                margin: "8px 0",

                "& .header": {
                  alignItems: "flex-end",
                  display: "flex",
                  justifyContent: "space-between",

                  "& .info": {
                    "& .plan-name": {
                      color: "#7B8AA2",
                      fontSize: "13px",
                      fontWeight: 400,
                      lineHeight: "15px",
                    },

                    "& .price": {
                      color: "#7B8AA2",
                      fontSize: "10px",
                      fontWeight: 400,
                      lineHeight: "12px",
                    },
                  },

                  "& .action": {
                    columnGap: "10px",
                    display: "flex",

                    "& button": {
                      alignItems: "center",
                      backgroundColor: "transparent",
                      color: "#7B8AA2",
                      cursor: "pointer",
                      fontSize: "11px",
                      fontWeight: 400,
                      lineHeight: "13px",
                      margin: 0,
                      padding: 0,
                      textDecoration: "underline",
                    },
                  },
                },

                "& .data": {
                  "& ul": {
                    borderTop: "1px solid rgba(36, 39, 44, 0.1)",
                    borderBottom: "1px solid rgba(36, 39, 44, 0.1)",
                    display: "flex",
                    flexDirection: "column",
                    marginTop: "10px",
                    padding: "14px 0 14px 20px",
                    listStyle: "none",

                    "& li": {
                      color: "#24272C",
                      display: "flex",
                      fontWeight: 400,
                      fontSize: "10px",
                      lineHeight: "10px",
                      marginBottom: "10px",
                      opacity: 0.7,
                      paddingLeft: "16px",
                      position: "relative",

                      "&::before": {
                        backgroundImage:
                          "url('data:image/svg+xml,%0A%3Csvg%20width%3D%2216%22%20height%3D%2214%22%20viewBox%3D%220%200%2016%2014%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20fill-rule%3D%22evenodd%22%20clip-rule%3D%22evenodd%22%20d%3D%22M15.0906%203.02981L5.44038%2013.2503L0.854492%207.82645L3.14538%205.88948L5.56378%208.74977L12.9093%200.970215L15.0906%203.02981Z%22%20fill%3D%22%2347C055%22%2F%3E%3C%2Fsvg%3E')",
                        backgroundSize: "9px 8px",
                        backgroundRepeat: "no-repeat",
                        backgroundPosition: "left center",
                        top: 0,
                        left: 0,
                        width: "9px",
                        height: "10px",
                        content: "''",
                        display: "block",
                        position: "absolute",
                      },
                    },
                  },
                },
              },
            },

            "& .pay-btn": {
              background: "#47C055",
              boxShadow: "0px 8px 8px rgba(50, 197, 255, 0.1)",
              borderRadius: "8px",
            },
          },
        },

        "& .already-signed-up": {
          display: "flex",
          justifyContent: "center",
          marginTop: "auto",

          "& h3": {
            color: "#24272C",
            fontSize: "18px",
            fontWeight: 500,
            lineHeight: "21px",

            "& a": {
              color: "#12B0EE",
              textDecoration: "none",
            },
          },
        },
      },
    },
  },

  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {
    "setup-process": {
      backgroundColor: "#ffffff",
    },

    setup: {
      paddingTop: "66px",
      minHeight: "max-content",

      "& .content": {
        display: "flex",
        height: "100%",
        justifyContent: "center",
        margin: "0 auto",
        width: "90%",

        "& .block-left": {
          display: "flex",
          height: "100%",
          justifyContent: "center",
          position: "relative",
          rowGap: "20px",
          flex: "1 0 auto",

          "& .block-fixed": {
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            height: "100%",
            justifyContent: "flex-start",
            position: "fixed",
          },

          "& h1": {
            color: "#3B414A",
            textAlign: "center",
            fontSize: "32px",
            fontWeight: 900,
            lineHeight: 1,
            marginTop: "85px",
            maxWidth: "400px",
          },

          "& .block-img": {
            maxWidth: "100%",
            height: "auto",
          },
        },

        "& .block-right": {
          borderLeft: "1px solid rgba(0, 0, 0, 0.1)",
          borderRight: "1px solid rgba(0, 0, 0, 0.1)",
          boxShadow: "0px 5px 40px rgba(18, 176, 238, 0.1)",
          flexShrink: 1,
          padding: "70px 50px",
          minHeight: "calc(100vh - 66px)",
          width: "420px",

          "& .already-signed-up": {
            marginTop: 0,
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    setup: {
      "& .content": {
        gap: "90px",
        padding: "0 90px",
        width: "80%",
      },
    },
  },
  "@media(min-width:1300px)": {
    setup: {
      "& .content": {
        // gap: "180px",
        // padding: "0 180px",
        maxWidth: "1300px",
      },
    },
  },
}));
