import React, { Fragment, useState, useEffect } from "react";
import { Helmet } from "react-helmet";
import { Link, useLocation } from "react-router-dom";
import moment from "moment";
import { Hidden } from "@material-ui/core";
import { useToastMessage } from "../../../contexts";

import scssClasses from "./paymentSuccess.module.css";
import Header from "../../../Components/Header/SignUpSetup";
import Check from "../../../assets/icons/successLogo.svg";
import RightArrow from "../../../assets/icons/rightArrow-1.svg";

import { showInvoiceById } from "../../../api/Invoice";
// styles
import useStyles from "../styles/muiStyles";
import image2 from "../assets/image-2.svg";

const PaymentSuccess = () => {
  const toastMessage = useToastMessage();
  const search = useLocation().search;

  const invoiceId = new URLSearchParams(search).get("id");

  const classes = useStyles(),
    [data, setData] = useState({
      nextMonthDate: "",
      amount: 0,
      nextPaymentDate: "",
    }),
    [nextPaymentAmt, setNextPaymentAmt] = useState(0);

  const showInvoiceDetail = async () => {
    const res = await showInvoiceById(invoiceId);
    if (!res.success) return toastMessage.setErrorMessage(res.message);

    let nextMonthDate = moment(res.payload.bill_to).format("DD/MMM/YYYY");
    let time = res.payload.created_at;
    time = new Date(time * 1000);
    setData({
      ...data,
      amount: res.payload.billing_amount.payable_amount,
      paymentDate: moment(time).format("DD/MMM/YYYY"),
      nextPaymentDate: nextMonthDate,
    });
  };

  useEffect(() => {
    showInvoiceDetail();
  }, []);

  useEffect(() => {
    let nexPaymentAmt = 0;
    let planDetail = JSON.parse(localStorage.getItem("selectedPlanDetail"));
    if (planDetail) {
      for (const plan of planDetail) {
        nexPaymentAmt += Number(plan.amount);
      }
      setNextPaymentAmt(nexPaymentAmt);
    }
    //localStorage.removeItem("selectedPlanDetail");
  }, []);

  const formatAmount = (amount) => {
    let amt = Number(amount);
    return amt
      .toFixed(2)
      .toString()
      .replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");
  };

  return (
    <Fragment>
      {/* Head */}
      <Helmet>
        <title>Payment Success - EasySocial.io</title>
        <link rel="canonical" href="https://easysocial.io/" />
        <meta
          name="keywords"
          content="Whatsapp chatbot ,automatic WhatsApp for business, Whatsapp AI, customizable whatsapp"
        />
        <meta
          name="description"
          content="EasySocial offers customizable and affordable WhatsApp chatbot solution for quick and hassle free functioning of your business."
        />
      </Helmet>
      <div className={classes["setup-process"]}>
        <Header />
        <section className={classes.setup}>
          <div className="content">
            <Hidden mdDown implementation="js">
              <div className="block-left">
                <div className="block-fixed">
                  <h1
                    dangerouslySetInnerHTML={{
                      __html: "",
                    }}
                  />
                  <img src={image2} alt="image2" className="block-img" />
                </div>
              </div>
            </Hidden>
            <div className="block-right">
              <div className={scssClasses.paymentSuccessWrapper}>
                <div className={scssClasses.firstHalf}>
                  <div className={scssClasses.successDivWrapper}>
                    <div className={scssClasses.successDivIcon}>
                      <img
                        src={Check}
                        alt=" success badge"
                        className={scssClasses.successBadge}
                      />
                    </div>
                    <div className={scssClasses.successTitle}>
                      Congratulations!
                    </div>
                    <div className={scssClasses.subTitleWrapper}>
                      <div className={scssClasses.subTitle}>
                        Thank you for subscribing
                      </div>
                      <div className={scssClasses.subTitle}>
                        Payment received for ₹ {formatAmount(data.amount)}
                      </div>
                    </div>
                    <Link to="/engage/chat" style={{ textDecoration: "none" }}>
                      <div className={scssClasses.buttonWrapIcon}>
                        <div>Let’s Get Started</div>
                        <div>
                          <img src={RightArrow} alt="right arrow" />
                        </div>
                      </div>
                    </Link>
                  </div>
                </div>
                <div className={scssClasses.secondHalf}>
                  <div className={scssClasses.paymentDetailsWrapper}>
                    <div className={scssClasses.purchaseWrapper}>
                      <div className={scssClasses.mainTitle}>
                        Purchase Details :
                      </div>
                      {/*                       <div className={scssClasses.box}>
                        <div className={scssClasses.title}>Users</div>
                        <div className={scssClasses.value}>3</div>
                      </div> */}
                      <div className={scssClasses.box}>
                        <div className={scssClasses.title}>Amount Paid</div>
                        <div className={scssClasses.value}>
                          ₹ {formatAmount(data.amount)}
                        </div>
                      </div>
                    </div>
                  </div>
                  {data.amount > 0 && (
                    <div className={scssClasses.recurringtDetailsWrapper}>
                      <div className={scssClasses.purchaseWrapper}>
                        <div className={scssClasses.mainTitle}>
                          Next Recurring :
                        </div>
                        <div className={scssClasses.box}>
                          <div className={scssClasses.title}>Date</div>
                          <div className={scssClasses.value}>
                            {data.nextPaymentDate}
                          </div>
                        </div>
                        <div className={scssClasses.box}>
                          <div className={scssClasses.title}>Amount</div>
                          <div className={scssClasses.value}>
                            ₹ {formatAmount(nextPaymentAmt)} (Tax Extra)
                          </div>
                        </div>
                      </div>
                    </div>
                  )}

                  <div className={scssClasses.unsubTitle}>
                    {"You can cancel subscrption anytime later"}
                    <br />
                    {"send out an email on cancel@easysocial.io"}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    </Fragment>
  );
};

export default PaymentSuccess;
